.movie-container {
  height: 35rem;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  margin: 0;
  padding: 0;
}

.recommends {
  background-color: var(--color-black-opacity);
  border-radius: 3rem;
  padding: 4rem 0rem 4rem 2rem;
}

.recommends h5 {
  color: var(--color-white);
}
